var today = new Date();
var hourNow = today.getHours();
var greeting;

if (hourNow > 18) {
    greeting = 'Good evening';
} else if (hourNow > 12) {  // Fixed: Should check `hourNow`, not `greeting`
    greeting = 'Good afternoon';
} else if (hourNow > 0) {  // Fixed: Should check `hourNow`, not `greeting`
    greeting = 'Good morning';
} else {
    greeting = 'Welcome';
}


document.getElementById("javascriptenter").innerText = greeting;